/*----FONTS----*/

/*----COLORS----*/

@import "../fonts/fonts.css";

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  background-color: #e4e5e7;
}

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

/*--------------------
				header
---------------------*/

.header {
  background: #fff;
}

.header .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.header .items .item {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
}

.header .header-up {
  border-bottom: 1px solid #666;
}

.header .header-up .items {
  min-height: 90px;
}

.header .header-up .items .item-left {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  width: calc(25% - 30px);
  padding-left: 0;
  margin-left: 15px;
  align-items: center;
  position: relative;
}

.header .header-up .items .item-left button {
  position: absolute;
  top: 50%;
  left: 4%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  transition: .2s;
  border: none;
  outline: none;
}

.header .header-up .items .item-left button:hover {
  transform: translate(-50%, -50%);
}

.header .header-up .items .item-left button:hover .line:nth-child(1) {
  top: 2px;
  left: 5px;
  transform: translateY(5px) rotate(-45deg);
}

.header .header-up .items .item-left button:hover .line:nth-child(3) {
  top: -2px;
  left: 5px;
  transform: translateY(-5px) rotate(45deg);
}

.header .header-up .items .item-left button:hover .line:nth-child(2) {
  opacity: 0;
}

.header .header-up .items .item-left .line {
  position: relative;
  height: 2px;
  width: 20px;
  top: 50%;
  left: 50%;
  background-color: #333366;
  display: block;
  margin: 5px 0;
  transition: .2s;
  transform: translate(-50%, -50%);
}

.header .header-up .items .item-left img {
  margin-left: 50px;
}

.header .header-up .items .item-center {
  width: calc(50% - 30px);
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.header .header-up .items .item-center ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .header-up .items .item-center li:not(:last-child) {
  margin-right: 30px;
}

.header .header-up .items .item-center a {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 14px;
  color: #343e5c;
  text-decoration: none;
}

.header .header-up .items .item-right {
  width: calc(25% - 30px);
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-right: 0;
  margin-right: 15px;
  align-items: center;
  justify-content: flex-end;
}

.header .header-up .items .item-right .cart {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.header .header-up .items .item-right .cart p {
  margin: 0 7px;
}

.header .header-up .items .item-right .cart span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border-radius: 25px;
  border: none;
  background: #e3e8f0;
  font-family: 'MontserratRegular', sans-serif;
  font-size: 11px;
  color: #343e5c;
}

.header .header-up .items .item-right button {
  padding: 10px 15px;
  border: 1px solid #e3e8f0;
  border-radius: 25px;
  color: #343e5c;
  text-transform: uppercase;
}

.header .header-up .items .item-right button:hover {
  color: #b9bed1;
  border: 1px solid #343e5c;
  transition: all 0.2s;
  cursor: pointer;
}

.header .header-down .items {
  min-height: 56px;
}

.header .header-down .items .item-left {
  width: calc(75% - 30px);
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 0;
  margin-left: 15px;
}

.header .header-down .items .item-left ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .header-down .items .item-left li:not(:last-child) {
  margin-right: 30px;
}

.header .header-down .items .item-left a {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 11px;
  color: #b9bed1;
  text-decoration: none;
  text-transform: uppercase;
}

.header .header-down .items .item-left a:hover {
  color: #343e5c;
}

.header .header-down .items .item-right {
  width: calc(25% - 30px);
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-right: 0;
  margin-right: 15px;
  align-items: center;
  justify-content: flex-end;
}

.header .header-down .items .item-right a {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 11px;
  text-transform: uppercase;
  color: #b9bed1;
  text-decoration: none;
}

.header .header-down .items .item-right .search {
  margin-left: 50px;
}

.header .header-down .items .item-right .search .img {
  margin-right: 10px;
}

.header .header-down .items .item-right .search input {
  width: 120px;
  border: none;
  font-family: 'MontserratRegular', sans-serif;
  font-size: 14px;
  color: #b9bed1;
}

/*--------------------
				banner
---------------------*/

.banner {
  background-color: #222328;
}

.banner .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  background: #222328 url(../img/project/iphone_6.png) no-repeat center;
  max-width: 1170px;
}

.banner .items .item {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
}

.banner .items .item-left {
  width: calc(41.66666666666667% - 30px);
  padding-left: 0;
  margin-left: 15px;
  padding-left: 100px;
  color: #ffffff;
}

.banner .items .item-left h2 {
  font-family: 'MontserratBlack', sans-serif;
  font-size: 42px;
  margin-top: 100px;
  margin-bottom: 20px;
}

.banner .items .item-left p {
  font-family: 'CrimsonTextRoman', sans-serif;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 60px;
}

.banner .items .item-left button {
  background-color: #3b55e6;
  padding: 15px 50px;
  border: none;
  border-radius: 25px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 100px;
}

.banner .items .item-left button:hover {
  color: #222328;
  transition: all 0.2s;
  cursor: pointer;
}

.banner .items .item-right {
  width: calc(50% - 30px);
  padding-right: 0;
  margin-right: 15px;
}

/*--------------------
				sort
---------------------*/

.sort {
  margin-top: 50px;
}

.sort .items {
  background: #fff;
  border-radius: 10px;
  margin: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'MontserratRegular', sans-serif;
  font-size: 11px;
  color: #a1a8bd;
  padding: 0 20px;
  min-height: 56px;
  text-transform: uppercase;
}

.sort .items input {
  border-radius: 15px;
  border: 1px solid #e3e8f0;
  background-color: #f0f2f7;
  color: #c7ccdc;
  margin: 0 10px;
  padding: 5px 14px;
}

.sort .items .input-key {
  width: 145px;
}

.sort .items .input-prise {
  width: 50px;
}

/*--------------------
				main
---------------------*/

.main {
  margin-top: 30px;
}

.main .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  /*--------------------
					filters
		---------------------*/
  /*--------------------
					products
		---------------------*/
  /*--------------------
					pagination
		---------------------*/
}

.main .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.main .items .item-left {
  width: calc(25% - 30px);
}

.main .items .item-left .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.main .items .item-left .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  background: #fff;
  border-radius: 10px;
  margin-bottom: 30px;
}

.main .items .item-left .items .item h2 {
  text-transform: uppercase;
}

.main .items .item-left .items .item h2,
.main .items .item-left .items .item li {
  border-bottom: 1px solid #f0f2f7;
  position: relative;
}

.main .items .item-left .items .item h2,
.main .items .item-left .items .item a,
.main .items .item-left .items .item p,
.main .items .item-left .items .item span {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 12px;
  color: #343e5c;
  text-decoration: none;
  line-height: 40px;
  padding: 0 20px;
}

.main .items .item-left .items .item span {
  color: #b8bfd3;
}

.main .items .item-left .items .item a:before {
  content: '\003E';
  position: absolute;
  right: 20px;
}

.main .items .item-left .items .item .form__list {
  display: flex;
  flex-direction: column;
}

.main .items .item-left .items .item .form__item {
  display: flex;
  justify-content: space-between;
}

.main .items .item-left .items .item input {
  margin-right: 20px;
}

.main .items .item-right {
  width: calc(75% - 30px);
}

.main .items .item-right .products .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.main .items .item-right .products .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
  background: #fff;
  border-radius: 10px;
  margin-bottom: 30px;
  padding: 20px;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.main .items .item-right .products .items .item h3 {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 16px;
  color: #222328;
  margin-top: auto;
  margin-bottom: 10px;
  line-height: 20px;
}

.main .items .item-right .products .items .item .h3sub {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 12px;
  color: #b9bed1;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.main .items .item-right .products .items .item .prise-new {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #3a405b;
  margin-bottom: 15px;
}

.main .items .item-right .products .items .item_big {
  width: calc(66.66666666666667% - 30px);
  padding: 0;
}

.main .items .item-down {
  background: #fff;
  width: calc(100% - 30px);
}

.main .items .item-down .pagination {
  display: flex;
  justify-content: center;
  list-style: none;
}

.main .items .item-down .pagination a {
  display: flex;
  justify-content: center;
  font-family: 'MontserratRegular', sans-serif;
  font-size: 14px;
  text-decoration: none;
  margin: 20px;
  color: #3a405b;
  line-height: 52px;
}

.main .items .item-down .pagination a:hover {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: #fff;
  color: #b9bed1;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  margin: 20px 0;
}

/*--------------------
			bestsellers
---------------------*/

.bestsellers {
  padding-bottom: 52px;
}

.bestsellers h4 {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 11px;
  color: #3a405b;
  background: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  padding: 24px 0;
}

.bestsellers .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.bestsellers .items .item {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(25% - 30px);
  background: #fff;
  border: 1px solid #a1a6bb;
  padding: 10px;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.bestsellers .items .item img {
  margin: auto 20px;
  width: 70%;
}

.bestsellers .items .item h3 {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 16px;
  color: #343e5c;
  margin-bottom: 10px;
}

.bestsellers .items .item .h3sub {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 11px;
  color: #a1a8bd;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.bestsellers .items .item-prise {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.bestsellers .items .item-prise .prise {
  margin-right: 20px;
  text-decoration: line-through;
  color: #b9bed1;
}

.bestsellers .items .item-prise .prise-new {
  color: #343e5c;
}

.bestsellers .item-left {
  margin-left: 15px;
}

.bestsellers .item-right {
  margin-right: 15px;
}

/*--------------------
			testimoials
---------------------*/

.testimoials {
  background-color: #fff;
  padding: 70px 0;
}

.testimoials h2 {
  font-family: 'MontserratBlack', sans-serif;
  font-size: 32px;
  color: #1c1e23;
  text-align: center;
  margin-bottom: 20px;
}

.testimoials .h2sub {
  font-family: 'CrimsonTextItalic', sans-serif;
  font-size: 16px;
  color: #b9bccf;
  margin-bottom: 50px;
  text-align: center;
}

.testimoials .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.testimoials .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.333333333333336% - 30px);
}

.testimoials .items .item-flex {
  display: flex;
}

.testimoials .items .item-flex .img {
  border-radius: 50%;
  margin-right: 20px;
  margin-bottom: 20px;
}

.testimoials .items .item-flex .name {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 14px;
  color: #3e425a;
  line-height: 20px;
}

.testimoials .items .item-flex .job {
  font-family: 'CrimsonTextItalic', sans-serif;
  font-size: 14px;
  color: #3e425a;
}

.testimoials .items .item .text {
  font-family: 'CrimsonTextRoman', sans-serif;
  font-size: 16px;
  color: #3e425a;
  line-height: 24px;
}

/*--------------------
				footer
---------------------*/

.footer {
  background: #222328;
}

.footer .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.footer .items .item {
  min-height: 150px;
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
}

.footer .footer-up .item-left {
  width: calc(16.666666666666668% - 30px);
  padding-left: 0;
  margin-left: 15px;
  display: flex;
  align-items: center;
}

.footer .footer-up .item-center,
.footer .footer-up ul {
  width: calc(66.66666666666667% - 30px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer .footer-up .item-center a,
.footer .footer-up ul a {
  font-family: 'MontserratRegular', sans-serif;
  font-size: 12px;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
}

.footer .footer-up .item-center li:not(:last-child),
.footer .footer-up ul li:not(:last-child) {
  margin-right: 30px;
}

.footer .footer-up .item-right {
  width: calc(16.666666666666668% - 30px);
  padding-right: 0;
  margin-right: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer .footer-up .item-right img {
  margin-left: 20px;
}

.footer .footer-down {
  border-top: 1px solid #333333;
}

.footer .footer-down .item-left {
  width: calc(50% - 30px);
  padding-left: 0;
  margin-left: 15px;
  display: flex;
  align-items: center;
  font-family: 'CrimsonTextItalic', sans-serif;
  font-size: 14px;
  color: #666;
  line-height: 22px;
}

.footer .footer-down .item-right {
  width: calc(50% - 30px);
  padding-right: 0;
  margin-right: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: #666;
}

.footer .footer-down .item-right p {
  margin-left: 25px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main .items .item-right .products .items .item {
    width: calc(50% - 30px);
  }

  .main .items .item-right .products .items .item_big {
    width: calc(100% - 30px);
  }
}

@media screen and (max-width: 720px) {
  .main .items .item-right .products .items .item {
    width: calc(100% - 30px);
  }

  .main .items .item-right .products .items .item_big {
    width: calc(100% - 30px);
  }
}