.lg(@name, @value){
	@media screen and (max-width: @break_lg){
		@{name}: @value;
	}
}

.lg-block(@content){
	@media screen and (max-width: @break_lg){
		@content();
	}
}

.md(@name, @value){
	@media screen and (max-width: @break_md){
		@{name}: @value;
	}
}

.md-block(@content){
	@media screen and (max-width: @break_md){
		@content();
	}
}

.sm(@name, @value){
	@media screen and (max-width: @break_sm){
		@{name}: @value;
	}
}

.sm-block(@content){
	@media screen and (max-width: @break_sm){
		@content();
	}
}

.xs(@name, @value){
